<template>
    <div class="content__week-day">
        <h3 class="content__week-title">{{ getWeekday }}</h3>
        <p class="content__week-date">{{ getDay }} {{ getMonth }}</p>
        <img :src="getIcon" alt="" class="content__week-icon">
        <p class="content__week-temp">
            <b>+{{ getTemp }}°</b>
            <span class="content__week-temp_night">+{{ getTempNight }}°</span>
        </p>
        <div class="content__week-info">{{ day?.weather?.[0]?.description }}</div>
    </div>
</template>

<script>
import { weatherIcons } from "@/assets/js/icons";
import unix from "@/assets/js/unix";

export default {
    name: "Day",
    props: {
        day: {
            type: Object,
            required: true
        }
    },
    computed: {
        getTemp() {
            return Math.round(this?.day?.temp?.day)
        },
        getTempNight() {
            return Math.round(this?.day?.temp?.night)
        },
        getIcon() {
            return weatherIcons[this.day?.weather?.[0]?.description] || weatherIcons["ясно"]
        },
        getDay() {
            return unix(this.day?.dt, 'day')
        },
        getMonth() {
            return unix(this.day?.dt, "month")
        },
        getWeekday() {
            return unix(this.day?.dt, "weekday")
        }
    }
}
</script>