import axios from 'axios'
import { createStore } from 'vuex'

export default createStore({
  state: {
    apiKey:"0c466cf5a6e56531316260a1ad697276",
    weatherDB: null,
  },
  getters: {
  },
  mutations: {
  },
  actions: {
    async getFetchWeather({state}, city){
      try {
        const weather = await axios.get(`http://api.openweathermap.org/geo/1.0/direct?q=${city}&appid=${state.apiKey}`)
        console.log(weather.data[0]);
        const{lat, lon} = weather.data[0];
        const part = "minutely, hourly, alerts"
        const weatherInfo = await axios.get(`https://api.openweathermap.org/data/2.8/onecall?lat=${lat}&lon=${lon}&exclude=${part}&units=metric&lang=ru&appid=${state.apiKey}`)
        // console.log(weatherInfo);
        state.weatherDB = {...weatherInfo.data, name: weather.data[0].name}
        console.log(state.weatherDB);
      } catch (error) {
        console.error(`Not Information ${error}`);
      }
    }
  },
  modules: {
  }
})
