<template>
    <nav class="nav">
        <div class="container">
            <a href="#!" class="nav__logo">
                <img src="../../assets/images/logo.svg" alt="">
                <h1>vue weather</h1>
            </a>
            <form action="" class="nav__form" @submit.prevent="getFetchWeather(searchCountry), searchCountry = ''">
                <img src="../../assets/images/kaplya.svg" alt="" @click="changeTheme">
                <input type="search..." class="nav__form-input" placeholder="Search Country..." v-model="searchCountry">
               
            </form>
        </div>
    </nav>
</template>

<script>
import { mapActions } from 'vuex';
function getFetchWeather() {
    console.log();
}
    export default {
        name:"Nav",
        data() {
            return {
                searchCountry: ""
            }
        },
        components:{},
        computed:{},
        watch:{},
        methods: {
            changeTheme(){
                if(body.classList.contains("light-theme")){
                    body.classList.remove("light-theme")
                    body.classList.add("dark-theme")
                    localStorage.theme = "dark-theme"
                }else{
                    body.classList.add("light-theme")
                    body.classList.remove("dark-theme")
                    localStorage.theme = "light-theme"
                }
            },
            ...mapActions(["getFetchWeather"])
        },
        mounted() {
            if(localStorage.theme === "dark-theme"){
                body.classList.remove("light-theme")
                body.classList.add("dark-theme")
            }else{
                body.classList.add("light-theme")
                body.classList.remove("dark-theme")
            }
        },
    }
</script>

<style lang="scss" scoped>

</style>